import Vue from "vue"

const vu = new Vue()
//成功提示
export const successAlert = (msg) => {
    vu.$message({
        showClose: true,
        message: msg,
        type: 'success'
    })
}
//警告提示
export const warningAlert = (msg) => {
    vu.$message({
        showClose: true,
        message: msg,
        type: 'warning'
    });
}


